<!-- 续排班查询 -->
<template>
  <Layout id="TalContinueSearch" class="gary">
    <TalContinueClass
      @exportSuccess="jump"
      platform="splat"
      @openClassDetail="showClassDetail($event.classId)"
      @openTeacherDetail="openTeacherDetail"
      />

    <TalClassDetail
      :class-id="classDetail.id"
      :show="classDetail.show"
      isDisplayExportBtn
      @close="classDetail.show = false"></TalClassDetail>
    <TalTeacherDetails
      ref="teacherRef"
      :schoolCode="schoolCode || ''"
      :teacherId="teacherDetail.id"
      :type="teacherDetail.type">
    </TalTeacherDetails>
  </Layout>
</template>

<script>
import ClassMixin from '../../../mixins/classDetail'
import TeacherDetialMixin from '../../../mixins/teacherDetail'
import {
  getTeacherByEmpNo
} from '@/api/teacher'
export default {
  name: 'applyContinue',
  mixins: [ClassMixin, TeacherDetialMixin],
  data () {
    return {
      schoolCode: ''
    }
  },
  methods: {
    openTeacherDetail (data) {
      /*
      * data
      *   schoolCode
      *   empNo
      *   type
      * */
      this.schoolCode = data.schoolCode
      getTeacherByEmpNo({ ...data }).then(res => {
        this.showTeacherDetail(data.schoolCode, res.id, '0')
      }).catch(err => {
        this.$Message.error('查询教师信息失败：' + err.message)
      })
    },
    jump () {
      this.$router.push({
        name: 'classExport'
      })
    }
  }
}
</script>

<style lang="less" scoped>
  #TalContinueSearch {
    overflow-x: hidden;
    padding: 10px;
  }
  .TalContinueSearch {
  }
</style>
